@use '../../foundation/mixin' as mixin;

////////Topページ////////// 

//Topページのファーストビューのグリッド
.p-gridcontainer {
    width: 100%;
    display: grid;
    grid-template-columns: calc(calc(100% - 280px - 40px) / 4) 
                           calc(calc(100% - 280px - 40px) / 4)
                           280px
                           calc(calc(100% - 280px - 40px) / 4)
                           calc(calc(100% - 280px - 40px) / 4);
    grid-template-rows: calc(calc(100vh - 350px - 20px) / 2)
                        280px
                        calc(calc(100vh - 350px - 20px) / 2);
    gap: 10px 10px;
    background-color: #F8F8F8;
    
    @include mixin.smartphone {
        width: 100%;
        height: 100vh;
        display: grid;
        grid-template-columns:calc(calc(100% - 125px) / 2)
                              125px 
                              calc(calc(100% - 125px) / 2);
        grid-template-rows: calc(calc(100vh - 125px) / 3)
                             125px                      
                             calc(calc(100vh - 125px) / 3)
                             calc(calc(100vh - 125px) / 3);

        gap: 0px 0px;

    }
}

.p-topsubtitle {
    @include mixin.verticalcenter;
    justify-content: space-between;

}

.p-topnumber {
    &__redline, &__yellowline,&__orangeline {
        position: relative;

        &::after {
            position: absolute;
            top:135%;
            left: 25%;
            content : "";
            width: 60px;
            height: 2px;
            transform: rotate(90deg);

            @include mixin.smartphone {
                top:155%;
                width: 30px;
                height: 1px;
            }
            
        }
    }

    &__redline::after {
        background-color: #FF3333;
    }

    &__yellowline::after {
        background-color: #FFD433 ;
    }

    &__orangeline::after {
        background-color: #FFAD33;
    }

} 

.p-topnotice {
    background:#F8F8F8 url(/content/img/ShinsaiBashi.png) no-repeat  600px/750px;
    background-position: calc(100% - 10px) 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background:#F8F8F8 url(/content/img/ShinsaiBashi.png) no-repeat  280px/340px;
        background-position: calc(100% - 20px) 20px;
    }

    &__text {
        width: 600px;
        height: 160px;

        @include mixin.verticalcenter;
        justify-content: space-between;

        @include mixin.smartphone {
            width: 300px;
            height: 300px;
        }

    }

   
}

.p-topnews {
    background:#fff url(/content/img/News.png) no-repeat  300px/600px;
    background-position: 10px 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background:#fff url(/content/img/News.png) no-repeat  185px/340px;
        background-position: 10px 30px;
    }

    &__contents {
        @include mixin.pc {
            width: 920px;
            display: flex;
            justify-content: space-between;
           
        }

        @include mixin.smartphone {
            display: flex;
            justify-content: center;
            flex-direction: column;
           
        }

    }
}

.p-newscard {
    width: 280px;
    height: 400px;
    border: #303030 solid 0.5px; 
    box-shadow: 2px 2px 4px rgba(48, 48, 48, 0.25);
    @include mixin.verticalcenter;

    >p {
        margin-top: 8px;
        height: 50px;
        width: 240px;
        font-weight: 700;
    }

    &__top {
        height: 47px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__categoly {
        width: 264px;
        display: flex;
        justify-content: space-between;

    }
}


//グルメ、ショッピング、ホテルの共通部分のスタイリング
.p-topfood__grid,.p-topshop__grid ,.p-tophotel__grid{
    @include mixin.pc {
        width: 920px;
        display: grid;
        row-gap:115px;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows : repeat(2, 1fr);
    }
    
}


.p-topfood {
    background:#F8F8F8 url(/content/img/Gourmet.png) no-repeat  300px/900px;
    background-position: calc(100% - 10px) 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background:#F8F8F8 url(/content/img/Gourmet.png) no-repeat  115px/340px;
        background-position: calc(100% - 10px) 20px;
    }

    &__gheader {
        height: 62px;
        width: 200px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__gtitle {
        height: 100%;
        display: flex;
        align-items: center;
    }
}

.p-topshop {
    background: #ffffff url(/content/img/Shopping.png) no-repeat  300px/1000px;
    background-position: 10px 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background: #ffffff url(/content/img/Shopping.png) no-repeat  105px/340px;
        background-position: 10px 30px;
    }


    &__contents, &__gtitle {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    &__contents {
        height: 345px;

        @include mixin.smartphone {
            height: 300px;
        }
    }

    &__gheader {
        width: 200px;
        display: flex;
        justify-content: space-between;
    }

    &__gtitle {
        height: 110px;

        @include mixin.smartphone {
            height: 86px;
        }
       
        >p {
            font-size: 24px;
            line-height: 1.5;

            @include mixin.smartphone {
                font-size: 16px;
            }
        }
    }
}

.p-tophotel {
    background:#F8F8F8 url(/content/img/hotel.png) no-repeat  300px/560px;
    background-position: calc(100% - 10px) 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background:#F8F8F8 url(/content/img/hotel.png) no-repeat  180px/340px;
        background-position: calc(100% - 10px) 20px;
    }

    &__grid {
        @include mixin.smartphone {
            @include mixin.verticalcenter;
        }
    }

    &__gheader {
        width: 200px;
        display: flex;
        justify-content: space-between;
        align-items: center;

    }

    &__contents {
        display: inline-block
    }

    &__text {
        @include mixin.verticalcenter;

        >p { line-height: 1.5;
        
        }
    }

}

.p-toprequest {
    background: #ffffff url(/content/img/toprequest/Request.png) no-repeat  300px/1000px;
    background-position: 10px 60px;
    @include mixin.verticalcenter;

    @include mixin.smartphone {
        background: #ffffff url(/content/img/toprequest/Request.png) no-repeat  145px/340px;
        background-position: 10px 30px;
    }

    &__text {
        @include mixin.verticalcenter;
        @include mixin.smartphone {
            width: 300px;
        }
        
    }
}

/////New一覧ページ/////////////////////////////////
.p-newsfirstview {
    height: 350px;
    width: 100%;
    background: url(/content/img/news/newsfirstview.webp) no-repeat  center/cover; 
    @include mixin.smartphone {
        height: 200px;
    }
}


.p-news {
    @include mixin.verticalcenter;
    background:#fff url(/content/img/News.png) no-repeat  300px/585px;
    background-position: 10px 60px;
    width: 100%;

    @include mixin.smartphone {
        background:#fff url(/content/img/News.png) no-repeat  80px/150px;
        background-position: 10px 20px;
    }

    &__grid {
        @include mixin.pc {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows : repeat(3, 1fr);
            gap: 80px 40px;
        }
        
    }
}

/////News詳細ページ/////////////////////////////////
.p-newsdetail {
    @include mixin.verticalcenter;
        &__header {
            width: 920px;
            display: flex;
            justify-content: space-between;

            @include mixin.smartphone {
                width: 300px;
            }

            >p {
                font-size: 36px;
                @include mixin.smartphone {
                    font-size: 16px;
                }

            }
        }

        &__title {
            @include mixin.smartphone {
                width: 300px;
            }
        }

        &__contents {
            width: 760px;
            height: 290px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @include mixin.smartphone {
                width: 300px;
            }
        }
    
}

/////Mainページ(グルメ、ショッピング、ホテル）////////////////////////
.p-mainfirstview {
    width: 100%;
    height: 500px;
    background: url(/content/img/main/main-map.webp) no-repeat  center/cover; 
}

.p-main {
    @include mixin.verticalcenter;
    background:#fff url(/content/img/main/ShinsaiBashi_main.png) no-repeat  600px/780px;
    background-position: 10px 60px;
    width: 100%;

        @include mixin.smartphone {
            background:#fff url(/content/img/ShinsaiBashi.png) no-repeat  80px/150px;
            background-position: 10px 20px;
        }

    &__tab {
        display: flex;
        width: 920px;
        justify-content: space-between;
    }

    &__orangetab, &__redtab, &__yellowtab {
        // display: block;
        background:grey;
        width: 300px;
        height: 50px;
        display: flex;
        justify-content: center;

        >a {
            display: block;
            width: 300px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 24px;


        }
    }

    //activeのクラスが着くと＃FFAD33に変わる。
    &__orangetab {
        &.active {
            background: #FFAD33;
        }
    }

    &__redtab {
        &.active { 
            background:#FF3333 ;
        }
    }

    &__yellowtab {
        &.active { 
            background:#FFD433 ;
        }
    }

    &__contents {
        display: none;/*はじめは非表示*/
	    opacity: 0;/*透過0*/
	    background: #fff;
        padding:50px 20px;
       
        
        &.is-active {
            display: block;/*表示*/
            animation-name: displayAnime;/*ふわっと表示させるためのアニメーション*/
            animation-duration: 2s;
            animation-fill-mode: forwards;
            background: transparent;
            @include mixin.verticalcenter;
        }
    }

    &__shoplinks {
        @include mixin.verticalcenter;
        background: transparent;

    }

    &__contentheader ,&__subtitle {
        font-size: 32px;
        display: flex;
        justify-content: space-between;
        

    }

    &__contentheader {
        width: 920px;
        align-items: center;
    }

    &__subtitle {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }

// グルメ、ショッピング、ホテルのタイトルナンバー
    &__subnumber, &__subshopnumber {
        display: flex;
        justify-content: space-between;
    }

    &__subnumber {
        width: 300px;
    }

    &__subshopnumber {
        width: 500px;
    }

    &__category {
        width: 220px;
    }

    &__shopcategory {
        width: 420px;
    }

    &__shoplink {
        @include mixin.pc {
            display: grid;
            grid-template-columns: repeat(2, 440px);
            grid-template-rows: repeat(2,70px);
        }
    }

    &__shop ,&__store{
        display: block;
        @include mixin.verticalcenter ;
        justify-content: center;

        &:nth-child(2),&:nth-child(3) {
            background-color:transparent;
        }
    }

    &__shop {
        background: rgba(255, 173, 51, 0.25);
    }

    &__store {
        background: rgba(255, 51, 51, 0.25);
    }

    &__shopname {
        display: flex;
        justify-content: space-between;
        width: 400px;

        > p {
            width: 200px;
        }

    }
 
}

// タブの切り替えアニメーション

@keyframes displayAnime {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}



.p-mainnumber{
    &__orange ,&__red { 
        font-size: 32px;
        position: relative;

        &::after {
            position: absolute;
            top: 50%;
            left: 200%;
            content : "";
            width: 40px;
            height: 1px;
        }

    }

    &__orange {
        color: #FFAD33;
        &::after {
            background: #FFAD33;    
        }
    }

    &__red {
        color: #FF3333;
        &::after {
            background: #FF3333;    
        }
    }
 
}



